.react-grid-layout{
}
.react-tabs__tab-list {
  margin: 0 0 10px;
  padding: 10px;
  display: flex;
  justify-content: center;
  border:none;
  border-radius:unset;
}
.react-tabs{
}
.react-tabs__tab {
  border:none;
  bottom: unset;
  display: flex;
  justify-content: center;
  align-items: center;
}

.react-tabs__tab--selected {
  border: none;
  border-radius: 2px;
  background: #c2c8cdcc;
}
.react-tabs__tab:focus:after{
  content:none;
}
.react-tabs__tab-panel{
}
.btn {
  background-color: lightgray;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-shadow: 0 0 4px #5e5e5ea1;
}
.navbtn{
  padding: 3px;
  margin: 2px;
}

#layoutdisplay {
  background-color: rgb(189, 190, 88);
width: 100%;
  height: 100vh;
}